
















































import Vue from 'vue'
import Header from "../components/Header.vue"

type Data = {
    error: boolean,
    message:string,
    id:number,
}

export default Vue.extend({
    name:"AddFriends",
    components: {
        Header
    },
    data(): Data{
        return {
            error: false,
            message:"",
            id:null,
        }
    },
    methods: {
        addFriend:async function () {
            const method = "POST"
            if (!this.id) {
                this.message = "id"
                this.error= !this.error
                return
            }
            const isFriend = this.$store.state.friends.filter(friend => friend.id == this.id)
            if (isFriend.length > 0){
                this.message = ""
                this.error = !this.error
                return
            }
            const json = {
                "friend_id":Number(this.id)
            }
            const body = JSON.stringify(json)
            const headers = {
                Authorization: `${this.$store.state.token}`,
                accept: "application/json",
                "Content-Type":"application/json",
            }

            const resposnse = await fetch(`${this.$store.state.baseUrl}/user/add_friend`,{method,body,headers})
            
            if (resposnse.status === 422){
                this.message = "id"
                this.error = !this.error
                return
            }
            const data = await resposnse.json()
            this.$store.commit("updateFriends",data)
            this.$router.push("/")
        }

    }
})
