












































type Data = {
  name: string;
  password: string;
  error: boolean;
};
import Vue from "vue";
export default Vue.extend({
  name: "Register",
  data(): Data {
    return {
      name: "",
      password: "",
      error: false
    };
  },
  props: {
    source: String,
  },
  methods: {
    login: async function (): Promise<void> {
      if (this.name === "" || this.password === "") return;
      if (!this.validLanguage(this.name)) return;
      const url = `${this.$store.state.baseUrl}user/register`
      const method = "POST"
       const headers = {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
        };
      const json = {
        username: this.name,
        password: this.password
      }
      const body = JSON.stringify(json)
      const result = await fetch(url,{method,headers,body});
      console.log(result)
      if (result.status === 422) alert("validError");
      if(result.status === 200) this.$router.push("/login")
    },
     validLanguage: function (str:string): boolean {
      return ( str.match(/^[A-Za-z0-9]*$/) )? true : false
    },
  },
});
